
import axios from 'axios'
// 
export const getDishPage = (params) => {
  return axios({
    url: '/api/dish/page',
    method: 'get',
    params
  })
}

// 
export const deleteDish = (ids) => {
  return axios({
    url: '/api/dish',
    method: 'delete',
    params: { ids }
  })
}

// 
export const editDish = (params) => {
  return axios({
    url: '/api/dish',
    method: 'put',
    data: { ...params }
  })
}

// 
export const addDish = (params) => {
  return axios({
    url: '/api/dish',
    method: 'post',
    data: { ...params }
  })
}

// 
export const queryDishById = (id) => {
  return axios({
    url: `/api/dish/${id}`,
    method: 'get'
  })
}

// 
export const getCategoryList = (params) => {
  return axios({
    url: '/api/category/list',
    method: 'get',
    params
  })
}

// 
export const queryDishList = (params) => {
  return axios({
    url: '/api/dish/list',
    method: 'get',
    params
  })
}

// down
export const commonDownload = (params) => {
  return axios({
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
    },
    url: '/api/common/download',
    method: 'get',
    params
  })
}

// ---
export const dishStatusByStatus = (params) => {
  return axios({
    url: `/api/dish/status/${params.status}`,
    method: 'post',
    params: { ids: params.id }
  })
}