import axios from 'axios'
// 
export const getCategoryPage = (params) => {
  return axios({
    url: '/api/category/page',
    method: 'get',
    params
  })
}

// 
export const queryCategoryById = (id) => {
  return axios({
    url: `/api/category/${id}`,
    method: 'get'
  })
}

// 
export const deleCategory = (ids) => {
  return axios({
    url: '/api/category',
    method: 'delete',
    params: { ids }
  })
}

// 
export const editCategory = (params) => {
  return axios({
    url: '/api/category',
    method: 'put',
    data: { ...params }
  })
}

// 
export const addCategory = (params) => {
  return axios({
    url: '/api/category',
    method: 'post',
    data: { ...params }
  })
}
