<template>
	<div class="dashboard-container" id="food-app">
		<div class="container" style="height:600px; overflow:auto">
			<div class="tableBar">
				<el-input v-model="input" placeholder="" style="width: 250px" clearable
					@keyup.enter.native="handleQuery">
					<i slot="prefix" class="el-input__icon el-icon-search" style="cursor: pointer" @click="init"></i>
				</el-input>
				<div class="tableLab">
					<span class="span-btn delBut non" @click="deleteHandle('', null)"></span>
					<span class="span-btn blueBug non" @click="statusHandle('1')"></span>
					<span style="border:none;" class="span-btn delBut non" @click="statusHandle('0')"></span>
					<el-button type="primary" @click="addFoodtype('add')">
						+ 
					</el-button>
				</div>
			</div>
			<el-table :data="tableData" stripe class="tableBox" @selection-change="handleSelectionChange">
				<el-table-column type="selection" width="25"></el-table-column>
				<el-table-column prop="name" label=""></el-table-column>
				<el-table-column prop="image" label="" align="center">
					<template slot-scope="{ row }">
						<el-image style="width: auto; height: 40px; border:none;cursor: pointer;"
							:src="getImage(row.image)" :preview-src-list="[ `/common/download?name=${row.image}` ]">
							<div slot="error" class="image-slot">
								<img src="../../assets/noImg.png" style="width: auto; height: 40px; border:none;">
							</div>
						</el-image>
					</template>
				</el-table-column>
				<el-table-column prop="categoryName" label=""></el-table-column>
				<el-table-column label="">
					<template slot-scope="scope">
						<span style="margin-right: 10px;">{{ scope.row.price }}</span>
					</template>
				</el-table-column>
				<el-table-column label="">
					<template slot-scope="scope">
						<span style="margin-right: 10px;">{{ scope.row.status == '0' ? '' : '' }}</span>
					</template>
				</el-table-column>
				<el-table-column prop="updateTime" label="">
				</el-table-column>
				<el-table-column label="" width="160" align="center">
					<template slot-scope="scope">
						<el-button type="text" size="small" class="blueBug" @click="addFoodtype(scope.row.id)">
							
						</el-button>
						<el-button type="text" size="small" class="blueBug" @click="statusHandle(scope.row)">
							{{ scope.row.status == '0' ? '' : '' }}
						</el-button>
						<el-button type="text" size="small" class="delBut non"
							@click="deleteHandle('', scope.row.id)">
							
						</el-button>
					</template>
				</el-table-column>
			</el-table>
			<el-pagination class="pageList" :page-sizes="[10, 20, 30, 40]" :page-size="pageSize"
				layout="total, sizes, prev, pager, next, jumper" :total="counts" @size-change="handleSizeChange"
				:current-page.sync="page" @current-change="handleCurrentChange"></el-pagination>
		</div>
	</div>
</template>

<script>
	import {
		getDishPage
	} from '../../api/food.js'

	import {
		deleteDish
	} from '../../api/food.js'
	import {
		editDish
	} from '../../api/food.js'
	import {
		addDish
	} from '../../api/food.js'
	import {
		queryDishById
	} from '../../api/food.js'
	import {
		getCategoryList
	} from '../../api/food.js'
	import {
		queryDishList
	} from '../../api/food.js'
	import {
		commonDownload
	} from '../../api/food.js'
	import {
		dishStatusByStatus
	} from '../../api/food.js'

	export default {
		name: 'flist',
		el: '#food-app',
		data() {
			return {
				input: '',
				counts: 0,
				page: 1,
				pageSize: 10,
				tableData: [],
				dishState: '',
				checkList: []
			}
		},
		computed: {},
		created() {
			this.init()
		},
		mounted() {},
		methods: {
			async init() {
				const params = {
					page: this.page,
					pageSize: this.pageSize,
					name: this.input ? this.input : undefined
				}
				await getDishPage(params).then(res => {
					if (String(res.data.code) === '1') {
						this.tableData = res.data.data.records || []
						this.counts = res.data.data.total
					}
				}).catch(err => {
					this.$message.error('' + err)
				})
			},
			getImage(image) {
				return `/api/common/download?name=${image}`
			},
			handleQuery() {
				this.page = 1;
				this.init();
			},
			// 
			addFoodtype(st) {
				if (st === 'add') {
					this.$router.push('/food/add')
			
					
				} else {
					this.$router.push({
						path: '/food/add',
						name: 'fadd',
						params: {
							id: st
						}
					})
				}
			},

			// 
			deleteHandle(type, id) {
				if (type === '' && id === null) {
					if (this.checkList.length === 0) {
						return this.$message.error('')
					}
				}
				this.$confirm(', ?', '', {
					'confirmButtonText': '',
					'cancelButtonText': '',
				}).then(() => {
					deleteDish(type === '' ? this.checkList.join(',') : id).then(res => {
						if (res.data.code === 1) {
							this.$message.success('')
							this.handleQuery()
						} else {
							this.$message.error(res.data.msg || '')
						}
					}).catch(err => {
						this.$message.error('' + err)
					})
				})
			},

			//
			statusHandle(row) {
				let params = {}
				if (typeof row === 'string') {
					if (this.checkList.length === 0) {
						this.$message.error('')
						return false
					}
					params.id = this.checkList.join(',')
					params.status = row
				} else {
					params.id = row.id
					params.status = row.status ? '0' : '1'
				}
				this.dishState = params
				this.$confirm('?', '', {
					'confirmButtonText': '',
					'cancelButtonText': '',
					'type': 'warning'
				}).then(() => {
					// ---
					dishStatusByStatus(this.dishState).then(res => {
						if (res.data.code === 1) {
							this.$message.success('')
							this.handleQuery()
						} else {
							this.$message.error(res.data.msg || '')
						}
					}).catch(err => {
						this.$message.error('' + err)
					})
				})
			},

			// 
			handleSelectionChange(val) {
				let checkArr = []
				val.forEach((n) => {
					checkArr.push(n.id)
				})
				this.checkList = checkArr
			},
			handleSizeChange(val) {
				this.pageSize = val
				this.init()
			},
			handleCurrentChange(val) {
				this.page = val
				this.init()
			}
		}
	}
</script>

<style>
</style>
